/* 表单样式
  标签使用form，样式使用.from，这两种写法具有相同的样式。默认是垂直布局

    一组表单元素通常包含一个label和一个或多个控件，使用.form-group把这一组元素包裹起来

    .form-group默认是块布局，如果希望在垂直表单中使用水平布局，把多个.form-group使用.form-inline包裹起来
    字段的名称：
      使用label标签，样式使用.control-label。label标签默认和.control-label具有相同样式

    字段对应的控件：
      控件可以直接跟在标签后面，如果希望能得到更好的排版，建议使用.control-group

    字段对应的查看内容：
      在详情页面表单中，表单元素对应不是控件，而是文本内容，文本内容推荐使用span标签，class有.control-text
      内容在区域右边并且右对齐

*/
