.group-dropdown .tri-down {
  display: inline-block;
  margin-right: 10pt;
  margin-left: -5pt;
  width: 0;
  height: 0;
  vertical-align: middle;
  border-color: transparent;
  border-style: solid;
  border-width: 8pt 7pt 0;
  border-top-color: #666;
  }
.dropdown-menu {
  margin-top: 10pt;
  width: 278pt;
  min-width: 0;
  font-size: 32pt;
  line-height: 1;
  border: none;
  }
.dropdown-menu.tri:before {
  position: absolute;
  top: -19pt;
  left: 45pt;
  width: 0;
  height: 0;
  content: "";
  border-color: transparent;
  border-style: solid;
  border-width: 0 15pt 20pt;
  border-bottom-color: rgba(51, 51, 51, .94);
  }
.dropdown-menu > li {
  border-bottom: 1px solid #000;
  }
.dropdown-menu > li:last-child {
  border-bottom: none;
  }
.dropdown-menu > li > a {
  padding: 25pt 48pt;
  line-height: 1;
  color: #fff;
  }
.dropdown-menu.black {
  background-color: rgba(51, 51, 51, .94);
  }
.autocomplete-list {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 999;
  display: block;
  width: 100%;
  min-height: calc(100% - 174pt);
  background-color: #fff;
  }
.user-list {
  padding-top: 86pt;
  padding-bottom: 98pt;
  }
.top-space {
  height: 20pt;
  background-color: #f3f3f3;
  }
.tab-content {
  padding-top: 188pt;
  padding-bottom: 98pt;
  }
.unwork {
  position: absolute;
  left: 0;
  padding-left: 130pt;
  width: 100%;
  height: 88pt;
  font-size: 28pt;
  line-height: 88pt;
  color: #6c5658;
  background-color: #eaeaea;
  }
